export class TitleBlock extends block {
    constructor(value, options){
        super(value, options)
    }

    toHTML(){
        return `
        <h1><span class="grn">${this.value}</span></h1>
    `
    }
}
export class RefBlock extends block {
    constructor(value, options){
        super(value, options)
    }

    toHTML(){
        const html = this.value.map(item=>`<a href="" >${item}</a><br>`)
        return `
        <div class="box1">
            ${html.join('')}
        </div>
        `
    }
}
export class ImageBlock extends block {
    constructor(value, options){
        super(value, options)
    }

    toHTML(){
        return `
        <button class="std-btn"><img src="${this.value}" alt=""></button>
        `
    }
}